<template>
  <div :class="isCheck?'checkbox-item checkbox-item-hove':'checkbox-item'">
      <span :class="isCheck?'icon-box '+item.icon+'_hover':'icon-box '+item.icon"></span>
      <p>{{item.text}}</p>
      <!-- <input type="checkbox" v-model="isCheck"/> -->
      <i v-if="isCheck"></i>
  </div>
</template>

<script>
	export default {
        name: 'CheckButtom',
        data() {
			return {
                isCheck:""
			}
        },
		props:["item","id"],
        watch: {
            id:function(val,oldval){
                if(val==this.item.id){
                    this.isCheck = true;
                }else{
                    this.isCheck = false;
                }
            }
        }
	}
</script>

<style lang="less" scoped>
.checkbox-item{
    position: relative;
    display: inline-block;
    width: 180px;
    border: 1px solid #DBE4ED;
    border-radius: 2px;
    height: 34px;
    line-height: 34px;
    margin: 10px 20px 10px 0;
    cursor: pointer;
    input[type='checkbox']{
        position: absolute;
        display: inline-block;
        opacity: 0;
        outline: none;
        width: 100%;
        height: 100%;
        cursor: pointer;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }
    i{
        position: absolute;
        display: inline-block;
        width: 20px;
        height: 20px;
        top: 0;
        right: 0;
        background: url(./images/icon_check.png) no-repeat center center;
    }
    p{
        font-family: MicrosoftYaHei;
        width: 160px;
        font-size: 14px;
        color: #6E6E6E;
        text-align: center;
    }
    .icon-box{
        display: inline-block;
        height: 24px;
        width: 24px;
        float: left;
        margin: 5px;
    }
    .icon_xc{
        background: url(./images/icon_xc.png) no-repeat center center;
    }
    .icon_xc_hover{
        background: url(./images/icon_xc_hover.png) no-repeat center center;
    }
    .icon_ghjyjl{
        background: url(./images/icon_ghjyjl.png) no-repeat center center;
    }
    .icon_ghjyjl_hover{
        background: url(./images/icon_ghjyjl_hover.png) no-repeat center center;
    }
    .icon_ghkqlx{
        background: url(./images/icon_ghkqlx.png) no-repeat center center;
    }
    .icon_ghkqlx_hover{
        background: url(./images/icon_ghkqlx_hover.png) no-repeat center center;
    }
    .icon_ghktlx{
        background: url(./images/icon_ghktlx.png) no-repeat center center;
    }
    .icon_ghktlx_hover{
        background: url(./images/icon_ghktlx_hover.png) no-repeat center center;
    }
    .icon_ghrylx{
        background: url(./images/icon_ghrylx.png) no-repeat center center;
    }
    .icon_ghrylx_hover{
        background: url(./images/icon_ghrylx_hover.png) no-repeat center center;
    }
    .icon_ghhhs{
        background: url(./images/icon_ghhhs.png) no-repeat center center;
    }
    .icon_ghhhs_hover{
        background: url(./images/icon_ghhhs_hover.png) no-repeat center center;
    }
    .icon_ghqscp{
        background: url(./images/icon_ghqscp.png) no-repeat center center;
    }
    .icon_ghqscp_hover{
        background: url(./images/icon_ghqscp_hover.png) no-repeat center center;
    }
    .icon_ghhscp{
        background: url(./images/icon_ghhscp.png) no-repeat center center;
    }
    .icon_ghhscp_hover{
        background: url(./images/icon_ghhscp_hover.png) no-repeat center center;
    }
    .icon_ghscp{
        background: url(./images/icon_ghscp.png) no-repeat center center;
    }
    .icon_ghscp_hover{
        background: url(./images/icon_ghscp_hover.png) no-repeat center center;
    }
    .icon_fdygp{
        background: url(./images/icon_fdygp.png) no-repeat center center;
    }
    .icon_fdygp_hover{
        background: url(./images/icon_fdygp_hover.png) no-repeat center center;
    }
    .icon_ghscy{
        background: url(./images/icon_ghscy.png) no-repeat center center;
    }
    .icon_ghscy_hover{
        background: url(./images/icon_ghscy_hover.png) no-repeat center center;
    }
    .icon_ghfdy{
        background: url(./images/icon_ghfdy.png) no-repeat center center;
    }
    .icon_ghfdy_hover{
        background: url(./images/icon_ghfdy_hover.png) no-repeat center center;
    }
    .icon_bsyx{
        background: url(./images/icon_bsyx.png) no-repeat center center;
    }
    .icon_bsyx_hover{
        background: url(./images/icon_bsyx_hover.png) no-repeat center center;
    }
    .icon_zxzly{
        background: url(./images/icon_zxzly.png) no-repeat center center;
    }
    .icon_zxzly_hover{
        background: url(./images/icon_zxzly_hover.png) no-repeat center center;
    }
    .icon_dp{
        background: url(./images/icon_dp.png) no-repeat center center;
    }
    .icon_dp_hover{
        background: url(./images/icon_dp_hover.png) no-repeat center center;
    }
    
}
.checkbox-item-hove{
    border: 1px solid #5681C2;
    p{
        color: #5681C2;
    }
}
</style>




