<template>
    <div class="pagination fr">
        <span class="prev" @click="goPrve(page)"></span>
        <span class="text-center">{{page.currentPage}}/{{page.totalPage}}</span>
        <span class="next" @click="goNext(page)"></span>
        <span class="page-info">共{{page.total}}条记录，共{{page.totalPage}}页</span>
    </div>
</template>

<script>
    export default {
        name: 'pagination',
        data() {
            return {
            }
        },
        props:["page"],
        methods: {
            goPrve:function(page){
                if(page.currentPage==1){
                    return false;
                }else{
                    page.currentPage--;
                    this.$emit("chengeCurrentPage",page)
                }
            },
            goNext:function(page){
                if(page.currentPage==page.totalPage){
                    return false;
                }else{
                    page.currentPage++;
                    this.$emit("chengeCurrentPage",page)
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .pagination{
        height: 20px;
        span{
            display: inline-block;
            height: 20px;
            line-height: 20px;
            float: left;
        }
        span.prev,span.next{
            width: 20px;
            cursor: pointer;
        }
        span.text-center{
            font-size: 14px;
            color: #6E6E6E;
            margin: 0 10px;
        }
        span.prev{
            background: url(./images/icon_prev.png) no-repeat center center
        }
        span.next{
            background: url(./images/icon_next.png) no-repeat center center
        }
        span.page-info{
            font-size: 14px;
            color: #6E6E6E;
            margin-left: 35px;
        }
    }
</style>




