<template>
  <div :class="isCheck?'checkbox-item checkbox-item-hove':'checkbox-item'">
      <span :class="isCheck?'icon-box '+item.icon+'_hover':'icon-box '+item.icon"></span>
      <p>{{item.text}}</p>
      <!-- <input type="checkbox" v-model="isCheck"/> -->
      <i v-if="isCheck"></i>
  </div>
</template>

<script>
	export default {
        name: 'CheckButtom',
        data() {
			return {
                isCheck:""
			}
        },
		props:["item","id"],
        watch: {
            id:function(val,oldval){
                if(val==this.item.id){
                    this.isCheck = true;
                }else{
                    this.isCheck = false;
                }
            }
        }
	}
</script>

<style lang="less" scoped>
.checkbox-item{
    position: relative;
    display: inline-block;
    width: 180px;
    border: 1px solid #DBE4ED;
    border-radius: 2px;
    height: 34px;
    line-height: 34px;
    margin: 10px 20px 10px 0;
    cursor: pointer;
    input[type='checkbox']{
        position: absolute;
        display: inline-block;
        opacity: 0;
        outline: none;
        width: 100%;
        height: 100%;
        cursor: pointer;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }
    i{
        position: absolute;
        display: inline-block;
        width: 20px;
        height: 20px;
        top: 0;
        right: 0;
        background: url(./images/icon_check.png) no-repeat center center;
    }
    p{
        font-family: MicrosoftYaHei;
        width: 160px;
        font-size: 14px;
        color: #6E6E6E;
        text-align: center;
    }
    .icon-box{
        display: inline-block;
        height: 24px;
        width: 24px;
        float: left;
        margin: 5px;
    }
    .icon_xc{
        background: url(./images/icon_xc.png) no-repeat center center;
    }
    .icon_xc_hover{
        background: url(./images/icon_xc_hover.png) no-repeat center center;
    }
    .icon_qcdl{
        background: url(./images/icon_qcdl.png) no-repeat center center;
    }
    .icon_qcdl_hover{
        background: url(./images/icon_qcdl_hover.png) no-repeat center center;
    }
    .icon_qmdj{
        background: url(./images/icon_qmdj.png) no-repeat center center;
    }
    .icon_qmdj_hover{
        background: url(./images/icon_qmdj_hover.png) no-repeat center center;
    }
    .icon_qcpg{
        background: url(./images/icon_qcpg.png) no-repeat center center;
    }
    .icon_qcpg_hover{
        background: url(./images/icon_qcpg_hover.png) no-repeat center center;
    }
    .icon_dmpg{
        background: url(./images/icon_dmpg.png) no-repeat center center;
    }
    .icon_dmpg_hover{
        background: url(./images/icon_dmpg_hover.png) no-repeat center center;
    }
    .icon_qcfy{
        background: url(./images/icon_qcfy.png) no-repeat center center;
    }
    .icon_qcfy_hover{
        background: url(./images/icon_qcfy_hover.png) no-repeat center center;
    }
    .icon_ltyh{
        background: url(./images/icon_ltyh.png) no-repeat center center;
    }
    .icon_ltyh_hover{
        background: url(./images/icon_ltyh_hover.png) no-repeat center center;
    }
    .icon_lgcx{
        background: url(./images/icon_lgcx.png) no-repeat center center;
    }
    .icon_lgcx_hover{
        background: url(./images/icon_lgcx_hover.png) no-repeat center center;
    }
    .icon_fdjcbmqx{
        background: url(./images/icon_fdjcbmqx.png) no-repeat center center;
    }
    .icon_fdjcbmqx_hover{
        background: url(./images/icon_fdjcbmqx_hover.png) no-repeat center center;
    }
    .icon_qcnsqjqx{
        background: url(./images/icon_qcnsqjqx.png) no-repeat center center;
    }
    .icon_qcnsqjqx_hover{
        background: url(./images/icon_qcnsqjqx_hover.png) no-repeat center center;
    }
    .icon_qdblqsdm{
        background: url(./images/icon_qdblqsdm.png) no-repeat center center;
    }
    .icon_qdblqsdm_hover{
        background: url(./images/icon_qdblqsdm_hover.png) no-repeat center center;
    }
    

}
.checkbox-item-hove{
    border: 1px solid #5681C2;
    p{
        color: #5681C2;
    }
}
</style>




