              </el-table-column>
              <el-table-column align='center' prop="partName" label="零件名称">
              </el-table-column>
              <el-table-column align='center' prop="price" label="4S店单价">
              </el-table-column>
              <el-table-column align='center' prop="ourPrice" label="本店单价">
              </el-table-column>
              <el-table-column align='center' prop="ourPrice" label="数量">
                <template slot-scope="scope">
                 
 <el-input-number class="input-number" v-model="num1" :min="1" :max="10" size="small"></el-input-number>
                </template>
              </el-table-column>
              <el-table-column align='center' prop="ourPrice" label="金额">
              </el-table-column>
              <el-table-column align='center' label="操作">
                <template slot-scope="scope">
                  <a class='delete'>删除</a>
                </template>
              </el-table-column>
            </el-table>
