        </el-tabs>
      </div>

    </div>
    <div class="table-footer-btn">
      <el-button type="primary">保存</el-button>
      <el-button type="primary">取消</el-button>
    </div>
  </div>

</template>


<script>
import headers from "@/components/Head";
import Pagination from "@/pages/deep/Pagination";
import CheckButtom from "@/pages/deep/CheckButtom";
export default {
  name: 'Deep',
  data() {
    return {
      dlList: [{
          icon: "icon_jqm",
          text: "节气门清洗"
        },

        {
          icon: "icon_jq",
          text: "进气系统清洗"
        },

        {
          icon: "icon_sy",
          text: "三元催化免拆清洗"
        },

        {
          icon: "icon_pyz",
          text: "喷油嘴清洗"
        },

        {
          icon: "icon_yxqx",
          text: "燃油系统清洗"
        },

        {
          icon: "icon_yxcs",
          text: "燃油系统除水保护"
        },
      ],
      ktList: [{
          icon: "icon_ktxd",
          text: " 空调消毒除味清洗"
        },

        {
          icon: "icon_ktsj",
          text: "空调风道杀菌免拆清洗"
        },

        {
          icon: "icon_ktzf",
          text: "空调蒸发箱清洗"
        },

        {
          icon: "icon_ktxd",
          text: "汽车空调可视护理"
        },
      ],
      lqList: [{
          icon: "icon_sx",
          text: "水箱系统清洗"
        },

        {
          icon: "icon_sx",
          text: "水箱止漏"
        },
        {
          icon: "icon_ghjyjl",
          text: "冷却系统保护"
        },
      ],
      rhList: [{
          icon: "icon_fdjjx",
          text: " 发动机润滑系统清洗"
        },

        {
          icon: "icon_fdjyh",
          text: "发动机润滑系统养护"
        },
