          icon: "icon_fdjkm",
          text: "发动机强力抗磨"
        },
      ],
      zdList: [{
        icon: "icon_sc",
        text: "刹车系统保养套餐"
      }, ],
      bsList: [{
          icon: "icon_bsbh",
          text: "自动变速箱保护"
        },

        {
          icon: "icon_bsqj",
          text: "自动变速箱清洁"
        },
