      zxList: [{
          icon: "icon_zxzly",
          text: "动力转向系统清洗"
        },

        {
          icon: "icon_zxzly",
          text: "动力转向系统保护"
        },

      ],
      qtList: [{
          icon: "icon_bls",
          text: "更换玻璃水"
        },

        {
          icon: "icon_lm",
          text: "更换冷媒"
        },
      ],

