      partTable: [{
        brand: '宝马',
        harNum: 'BM 11427640862-A',
        partNum: '11427640862',
        partName: '组件机油滤清器滤芯',
        price: '240.00',
        ourPrice: '140.00',
      }, {
        brand: '嘉实多',
        harNum: 'CASTPOL JHZX 0W40-209L',
        partNum: 'MO201543',
