        value: '选项1',
        label: '胎面宽'
      }, {
        value: '选项2',
        label: '胎面宽'
      }, {
        value: '选项3',
        label: '胎面宽'
      }],
      value: '',
      num1: 1,
      activeName: 'first',
      search: '',
      page1: {
        total: 196,
        currentPage: 1,
        totalPage: 6
      },
    }
  },
  components: {
    headers,
    CheckButtom,
    Pagination
  },
  methods: {
    chengeCurrentPage1: function (page) {
      this.page1 = page;
      console.log(this.page1)
    },
    expand() {
      var _self = this;
      _self.expandState = !_self.expandState;
    }
