<template>
	<div class="partErrorCorrectionHistory">
		<headers></headers>
            <div class="bread-crumb">
                <span class="crumb-item">配件维护</span>
            </div>
		<div class="main">
			<div class="container">
                <el-form :model="form1" :label-position="labelPosition" label-width="100px">
                    <el-col :span="8">
                        <el-form-item label="配件品牌">
                            <el-input placeholder="" v-model="inputVal1"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="8">
                        <el-form-item label="和谐编号">
                            <el-input placeholder="BM 534343455545" v-model="inputVal2"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="8">
                        <el-form-item label="零件号">
                            <el-input placeholder="BM 534343455545" v-model="inputVal3"></el-input>
                        </el-form-item>
                    </el-col>
                </el-form>

                <el-form :model="form2" :label-position="labelPosition" label-width="100px">
                    <el-col :span="8">
                        <el-form-item label="配件名称">
                            <el-input placeholder="BM 534343455545" v-model="inputVal4"></el-input>
                        </el-form-item>
                    </el-col>
                </el-form>

                <div class="btnBox">
					<el-button type="primary">查询</el-button>
					<el-button size="mini" @click="resetForm">重置</el-button>
				</div>

                <div class="tableBox">
                    <el-table :data="tableData" border style="width: 100%;">
                        <el-table-column prop="storeName" label="门店" align="center" min-width="180">
                        </el-table-column>
                        <el-table-column prop="contacts" label="联系人" align="center" min-width="150">
                        </el-table-column>
                        <el-table-column prop="phoneNo" label="手机号" align="center" min-width="150">
                        </el-table-column>
                        <el-table-column prop="corrType" label="纠错类型" align="center" min-width="150">
                        </el-table-column>
                        <el-table-column prop="submitTime" label="提交时间" align="center" min-width="150">
                        </el-table-column>
                        <el-table-column label="操作" align="center" width="100">
                            <template slot-scope="scope">
                                <el-button @click="showDialog" type="text" size="small">明细</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
			</div>
		</div>
        
        <!-- 弹窗 -->
        <el-dialog title="适用车型错误" :visible.sync="isShowDialog" class="edite-dialog" width="1000px" center :modal=false>
            <div class="dialog-content">
                <p class="dotRow">配件信息</p>
                <el-form ref="form" :model="form3" :label-position="labelPosition" label-width="100px" class="part-error-correct-form">
                    <el-form-item label="VIN号">
                        <p class="iptResult">fffdgf</p>
                    </el-form-item>

                    <el-form-item label="车辆车型">
                        <p class="iptResult">fdfgfgg</p>
                    </el-form-item>
                </el-form>

                <p class="dotRow">车型信息</p>
                <el-form :model="form4" :label-position="labelPosition" label-width="100px">
                    <el-col :span="12">
                        <el-form-item label="采购价">
                            <p class="iptResult">BM 11427640862-A</p>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="利润率">
                            <p class="iptResult">55%</p>
                        </el-form-item>
                    </el-col>
                </el-form>

                <p class="dotRow">照片信息</p>
                <el-form ref="form" :model="form5" :label-position="labelPosition" label-width="100px" class="part-error-correct-form">
                    <el-form-item label="原车配件照片">
                        <img class="photo" src="../../assets/images/img1.jpg" alt="">
                    </el-form-item>

                    <el-form-item label="无法正确安装的配件照片">
                        <img class="photo" src="../../assets/images/img1.jpg" alt="">
                    </el-form-item>
                </el-form>

            </div>
            <div class="btnBox">
                <el-button type="primary" @click="isShowDialog=false">确定</el-button>
            </div>
        </el-dialog>

	</div>
</template>

<script>
    import headers from "@/components/Head";
    export default {
        name: "partErrorCorrectionHistory",
        data() {
            return {
                form1: null,
                form2: null,
                form3: null,
                form4: null,
                form5: null,
                inputVal1: '',
                inputVal2: '',
                inputVal3: '',
                inputVal4: '',
                labelPosition: 'right',
                errorInfo: '',
                isShowDialog:false,
                tableData: [{
                    storeName: "上海七宝第一分部",
                    contacts: "陈大宝",
                    phoneNo: "11427640862",
                    corrType: "未知错误",
                    submitTime: "2017-11-12"
                }, {
                    storeName: "北京海淀第一分部",
                    contacts: "嘉宜美",
                    phoneNo: "11427640862",
                    corrType: "未知错误",
                    submitTime: "2017-11-02"
                }]
            }
        },
        components: { headers },
        methods: {
            showDialog () {
                this.isShowDialog = true;
            },
            resetForm () {
                form1.inputVal1 = '';
                form1.inputVal2 = '';
                form1.inputVal3 = '';
                form2.inputVal4 = '';
            }
        },
        watch: {}
    };
</script>

<style lang="less">
.bread-crumb{
    height: 50px;
    background: #fff;
    margin-top: 10px;
    padding: 0 35px;
    span{
        display: block;
        float: left;
        margin-right: 5px;
        line-height: 50px;
        height: 50px;
    }
    .crumb-item{
        padding-right:15px;
        font-size: 16px;
        color: #5681C2;
        font-weight: 700;
    }
}
.main{
    padding: 0 20px;
    .container {
        margin: 10px auto 0;
        padding: 20px 20px 280px 0;
        width: 100%;
        height: auto;
        background: #fff;
        border-radius: 2px;
        box-sizing: border-box;
        overflow: hidden;
        .btnBox{
            margin: 20px auto;
            display: flex;
            justify-content: space-between;
            clear: both;
            width: 240px;
            .el-button--primary{
                padding: 7px 13px;
            }
            .el-button--mini{
                padding: 7px 13px;
                border: 1px solid #5681C2;
                border-radius: 4px;
                font-size: 14px;
                color: #6E6E6E;
                background: none;
            }
        }
        .tableBox{
            margin-left: 20px;
            padding-top: 14px;
            border-top: 2px solid #C1D1E5;
            .el-table{
                font-size: 12px;
                color: #6E6E6E;
                line-height: 14px;

            }
            .el-table thead{
                font-size: 14px;
                color: #2E2E2E;
            }
            .el-table th.is-leaf{
                border-bottom-color: #C1D1E5;
            }
            .el-button--text{
                text-decoration: underline;
                font-size: 12px;
                color: #5681C2;
            }
        }
    }
    
}
.partErrorCorrectionHistory{
    .el-input__inner{
        padding: 0 10px;
    }
    .el-dialog__header{
        padding: 0 14px;
        height: 48px;
        line-height: 48px;
        text-align: left;
        border-bottom: 1px solid #C1D1E5;
        box-sizing: border-box;
        .el-dialog__title{
            font-size: 14px;
        color: #2E2E2E;
        }
        .el-dialog__close{
            color: #BFBDBD;
        }
    }
    .el-dialog__body{
        padding-top: 6px;
    }
    .dialog-content{
        .dotRow {
            position: relative;
            padding-left: 18px;
            font-size: 14px;
            line-height: 50px;
            color: #6E6E6E;
            clear: both;
            &:before {
                content: "";
                position: absolute;
                left: 0;
                top: 21px;
                width: 8px;
                height: 8px;
                border-radius: 50%;
                background: #C1CCD8;
            }
        }
        .el-form-item__label{
            color: #2E2E2E;
        }
        .iptResult{
            color: #6E6E6E;
            font-size: 12px;
        }
        .photo{
            margin-top: 15px;
            width: 100px;
            height: 100px;
        }
    }
    .btnBox {
        display: flex;
        justify-content: center;
        width: 100%;
    }

}



@media screen and (max-width: 1366px) {
		.page-container  {
				width: 96%;
				padding: 0 2%;
		}
}
</style>


