			padding-right: 15px;
			font-size: 16px;
			color: #BFBDBD;
			font-weight: 700;
			background: url(./images/icon_arrow_right_cur.png) no-repeat center right;
		}
		.current-crumb {
			font-size: 16px;
			color: #5681C2;
		}
	}
	
	.edite-dialog {
