<template>
	<div class="head">
        <img src="../assets/images/logo.png" alt="">
        <div class="close" @click="closeWindow"><img src="../assets/images/powerOff.png">&nbsp;关闭</div>
	</div>
</template>

<script>
	export default {
		name: 'Head',
		data() {
			return {
                
			}
        },
        created() {
            
        },
        methods: {
            closeWindow () {
                window.close();
            }
        },
        watch: {

        }
	}
</script>

<style scoped>
    .head{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 24px 0 32px;
        width: 100%;
        height: 76px;
        background: #fff;
        box-shadow: 0 3px 4px 0 rgba(14,65,117,0.10);
        box-sizing: border-box;
    }
    .close{
        font-size: 14px;
        color: #8A8A8A;
        cursor: pointer;
    }
</style>


