<template>
  <div class="page">

    <headers></headers>
    <div class="crumb">
      <span>工单助手</span>
      <i class="el-icon-arrow-right"></i>
      <span class="current-crumb">轮胎</span>
    </div>
    <div class="main">
      <div class='tire-menu'>
        <table class="tire-menu-table">
          <tr>
            <td class="tire-menu-title">轮胎规格</td>
            <td class="tire-menu-format">
              <div class="tire-format-img">
                <img src='/static/tire.png' />
                <el-select v-model="value" placeholder="请选择" size='mini'>
                  <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
                <el-select v-model="value2" placeholder="请选择" size='mini'>
                  <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
                <el-select v-model="value3" placeholder="请选择" size='mini'>
                  <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </div>

              <div class="usual-format">
                <span>常用规格：</span>
                <a>195/65R15</a>
                <a>205/55R16</a>
                <a>195/55R15</a>
                <a>185/60R14</a>
                <a>195/60R15</a>
                <a>215/70R15</a>
              </div>
            </td>
          </tr>
          <tr>
            <td class="tire-menu-title">轮胎品牌</td>
            <td>
              <div class="tire-menu-choose">
                <a class="no-limit">不限</a>

                <a>米其林</a>

                <a>普利司通</a>

                <a>邓禄普</a>

                <a>马牌</a>

                <a>韩泰</a>

                <a>将军</a>

                <a>回力</a>
                <a class="more first">更多
                  <i class="el-icon-arrow-down"></i>
                </a>
              </div>
            </td>
          </tr>
          <tr>
            <td class="tire-menu-title">载重指数</td>
            <td>
              <div class="tire-menu-choose">
                <a class="no-limit">不限</a>
                <a>93</a>
                <a>100</a>
                <a>101</a>
                <a>102</a>
                <a>103 </a>
                <a class="more">更多
                  <i class="el-icon-arrow-down"></i>
                </a>
              </div>
            </td>
          </tr>
          <tr>
            <td class="tire-menu-title">速度级别</td>
            <td>
              <div class="tire-menu-choose">
                <a class="no-limit">不限</a>
                <a>H</a>
                <a>N</a>
                <a>P</a>
                <a>Q</a>
                <a>R</a>
                <a>S</a>
                <a>T</a>
                <a>T/H</a>
                <a>V</a>
                <a>V/H</a>
                <a>W</a>
                <a>Y</a>
                <a class="more">更多
                  <i class="el-icon-arrow-down"></i>
                </a>
              </div>
            </td>
          </tr>
          <tr>
            <td class="tire-menu-title">轮胎花纹</td>
            <td>
              <div class="tire-menu-choose">
                <a class="no-limit">不限</a>
                <a>1</a>
                <a>2</a>
                <a>3</a>
                <a>4</a>
                <a>5</a>
                <a>6</a>
                <a class="more">更多
                  <i class="el-icon-arrow-down"></i>
                </a>
              </div>
            </td>
          </tr>
          <tr>
            <td class="tire-menu-title">轮胎特性</td>
            <td>
              <div class="tire-menu-choose">
                <a class="no-limit">不限</a>
                <a>防爆胎(SSR\RFT\ZP)</a>
                <a>雪地胎(M+S)</a>
                <a class="more">更多
                  <i class="el-icon-arrow-down"></i>
                </a>
              </div>
            </td>
          </tr>
        </table>


      </div>
      <div class='tire-content'>
        <h3 class="has-choose">已选择
          <span class="red">1</span>个商品</h3>
        <el-table ref="multipleTable" :data="partTable" border tooltip-effect="dark" style="width: 100%">
          <el-table-column type="selection" align='center'>
          </el-table-column>
          <el-table-column label="品牌" width="100" align='center'>
            <template slot-scope="scope">{{ scope.row.brand }}</template>
          </el-table-column>
          <el-table-column prop="harNum" label="和谐编号" align='center'>
          </el-table-column>
          <el-table-column prop="partNum" label="零件号" align='center'>
          </el-table-column>
          <el-table-column prop="partName" label="零件名称" align='center'>
          </el-table-column>
          <el-table-column prop="price" label="4S店单价" align='center'>
          </el-table-column>
          <el-table-column prop="ourPrice" label="本店单价" align='center'>
          </el-table-column>
          <el-table-column prop="ourPrice" label="数量" align='center'>
            <template slot-scope="scope">
            
              <el-input-number class="input-number" v-model="num1" :min="1" :max="10" size="small"></el-input-number>
            </template>
          </el-table-column>
          <el-table-column prop="ourPrice" label="金额" align='center'>
          </el-table-column>
        </el-table>
        <div class='tire-footer'>
          <span>
            <Pagination :page="page1" @chengeCurrentPage="chengeCurrentPage1"></Pagination>
          </span>
        </div>

      </div>

    </div>
    <div class="table-footer-btn">
      <el-button type="primary">保存</el-button>
      <el-button type="primary">取消</el-button>
    </div>
  </div>

</template>




<script>
import headers from "@/components/Head";
import Pagination from "@/pages/tire/Pagination";
export default {
  name: 'Tire',
  data() {
    return {
      partTable: [{
        brand: '宝马',
        harNum: 'BM 11427640862-A',
        partNum: '11427640862',
        partName: '组件机油滤清器滤芯',
        price: '240.00',
        ourPrice: '140.00',
      }, {
        brand: '嘉实多',
        harNum: 'CASTPOL JHZX 0W40-209L',
        partNum: 'MO201543',
        partName: '嘉实多机油 极护专享0W-40 A3 …',
        price: '298.50',
        ourPrice: '78.50',
      }],
      options: [{
        value: '选项1',
        label: '胎面宽'
      }, {
        value: '选项2',
        label: '选项2'
      }, {
        value: '选项3',
        label: '选项3'
      }],   options2: [{
        value: '选项1',
        label: '扁平比'
      }, {
        value: '选项2',
        label: '选项2'
      }, {
        value: '选项3',
        label: '选项3'
      }],   options3: [{
        value: '选项1',
        label: '直径'
      }, {
        value: '选项2',
        label: '选项2'
      }, {
        value: '选项3',
        label: '选项3'
      }],
      value: '胎面宽',
      value2: '扁平比',
      value3: '直径',
      num1: 1,
      page1: {
        total: 196,
        currentPage: 1,
        totalPage: 6
      },

    }
  },
  components: {
    headers,
    Pagination
  },
  methods: {
    chengeCurrentPage1: function (page) {
      this.page1 = page;
      console.log(this.page1)
    },
  }
}


</script>
<style lang="less" >
.page {
  text-align: left;
  min-width: 1200px;
}

.red {
  color: #C91523;
}

.crumb {
  text-align: left;
  height: 50px;
  line-height: 50px;
  background: #fff;
  margin-top: 10px;
  padding: 0 35px;
  font-size: 16px;
  color: #BFBDBD;
  span {
    margin-right: 5px;
  }
  i {
    font-size: 10px;
    color: #5681C2;
  }
  .current-crumb {
    color: #5681C2;
  }
}

.main {
  background-color: #EBF1FA;
  padding: 20px;
}

.tire-menu {
  background: #fff;
  padding: 0 14px;
  margin-bottom: 14px;
}

.tire-content {
  background: #fff;
  padding: 15px 14px;
}

.menu-ul li {
  border-bottom: 1px solid #DBE4ED;
}

.menu-name {
  border-right: 1px solid #DBE4ED;
  width: 104px;
  padding: 17px 0;
  padding-left: 18px;
}

.tire-menu-table {
  width: 100%;
  color: #2E2E2E;
  a {
    color: #2E2E2E;
  }
  td img {
    margin-left: 45px;
    display: block;
  }
  td {
    padding: 17px 15px;
    border-bottom: 1px solid #DBE4ED;
    text-align: left;
  }
  tr:last-child td {
    border-bottom: 0;
  }
  .el-select {
    width: 80px;
  }
  .tire-menu-title {
    width: 105px;
    color: #6E6E6E;
    border-right: 1px solid #DBE4ED;
    border-bottom: 1px solid #DBE4ED;
  }
}

.usual-format {
  background: #FAFCFF;
  padding: 15px;
  a {
    margin-right: 40px;
  }
}

.tire-format-img {
  padding: 17px 15px;
  .el-select {
    margin-left: 20px;
  }
  .el-input--mini .el-input__inner {
    color: #000;
    height: 24px;
    padding: 10px;
  }
}

.el-select-dropdown__item {
  font-size: 12px;
  height: 28px;
  line-height: 28px;
}

h3.has-choose {
  text-align: right;
  color: #6E6E6E;
  margin-bottom: 18px;
}

td.tire-menu-format {
  padding: 0;
}

.tire-menu-choose {
  a {
    cursor: pointer;
    margin-right: 70px;
  }
  a.no-limit {
    margin-right: 60px;
  }
  a.more {
    float: right;
    color: #BFBDBD;
    margin-right: 0px;
  }
  a.more.first {
    color: #2E2E2E;
  }
}

.page {
  margin-top: 16px;
  text-align: right;
}

.tire-content {
  .el-checkbox__input.is-checked .el-checkbox__inner,
  .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #fff;
  }
  .el-checkbox__inner::after {
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    content: "";
    border: 1px solid #5681C2;
    border-left: 0;
    border-top: 0;
    height: 7px;
    left: 4px;
    position: absolute;
    top: 1px;
    -webkit-transform: rotate(45deg) scaleY(0);
    transform: rotate(45deg) scaleY(0);
    width: 3px;
    -webkit-transition: -webkit-transform .15s cubic-bezier(.71, -.46, .88, .6) 50ms;
    transition: -webkit-transform .15s cubic-bezier(.71, -.46, .88, .6) 50ms;
    transition: transform .15s cubic-bezier(.71, -.46, .88, .6) 50ms;
    transition: transform .15s cubic-bezier(.71, -.46, .88, .6) 50ms, -webkit-transform .15s cubic-bezier(.71, -.46, .88, .6) 50ms;
    -webkit-transform-origin: center;
    transform-origin: center;
  }
  .el-table {
    border: 1px solid #DBE4ED;
    border-radius: 0 4px 4px 4px;
    .cell {
      font-size: 12px;
      color: #6E6E6E;
    }
    th>.cell {
      color: #2E2E2E;
      font-size: 14px;
    }
  }
  .input-number {
 
    width: 120px;

.el-input-number__decrease, .el-input-number__increase {
    width: 30px;
    font-size: 13px;
    background:transparent;
}
  .el-input__inner {
  border-radius: 0;
}
  }
  .el-table__body-wrapper {
    overflow: hidden;
  }

}

.tire-footer {
  overflow: hidden;
  margin-top: 16px;
}

.table-footer-btn {
  padding-bottom: 44px;
  text-align: center;
  button:first-child {
    margin-right: 50px;
  }
  button:last-child {
    margin-left: 50px;
  }
}



</style>





