<template>
    <div class="new-parts-maintain" v-show="showOrHide">
        <div class="base-info block-item">
            <div class="header">
                <img src="./images/left.png" alt="">
                <span class="header-name">基础信息</span>
            </div>
            <div class="parts-hr"></div>
            <div class="info-content">
                <el-form :model="form" :rules="rules" label-width="100px" class="parts-form-inline">
                    <div class="item-row">
                        <el-form-item label="配件类型" prop="partsType">
                            <template v-if="editOrNew">
                                <el-select :key="1" v-model="form.basicInfo.partsType" placeholder="请选择配件类型">
                                    <el-option :label="item.name" :key="item.value" v-for="item in form.partsTypeTree"
                                            :value="item.value"></el-option>
                                </el-select>
                            </template>
                            <template v-else="!editOrNew"><span :key="2">{{ form.basicInfo.partsType || "宝马" }}</span></template>
                        </el-form-item>
                        <el-form-item label="配件品牌" prop="partsBrand">
                            <el-select v-model="form.partsBrand" placeholder="请选择配件品牌">
                                <el-option :label="item.name"
                                        v-for="item in form.partsBrand"
                                        :key="item.value"
                                        :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="和谐编号" prop="number">
                            <template v-if="editOrNew">
                                <el-input v-model="form.basicInfo.number" placeholder=""></el-input>
                            </template>
                            <template v-else="!editOrNew">{{ form.basicInfo.number || "232335454645" }}</template>

                        </el-form-item>
                        <el-form-item label="零件号">
                            <el-input v-model="form.basicInfo.tempNo" placeholder="原厂件请输入OE号 品牌件请输入OEM号"></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="质量标准" prop="quality">
                            <el-select v-model="form.basicInfo.quality" placeholder="">
                                <el-option :label="item.name"
                                        :key="item.value"
                                        v-for="item in form.quality"
                                        :value="item.value"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="配件图号">
                            <el-input v-model="form.basicInfo.partsPic" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="配件名称" prop="partsName">
                            <el-input v-model="form.basicInfo.partsName" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="配件俗称">
                            <el-input v-model="form.basicInfo.partsNikName" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="包装规格">
                            <el-input v-model="form.basicInfo.specValue" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="计量单位">
                            <el-input v-model="form.basicInfo.unitName" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="备注">
                            <el-input v-model="form.basicInfo.note" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="状态">
                            <el-select v-model="form.basicInfo.status" placeholder="">
                                <el-option label="启用" value="1"></el-option>
                                <el-option label="停用" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                </el-form>
            </div>
        </div>
        <div class="price block-item">
            <div class="header">
                <img src="./images/left.png" alt="">
                <span class="header-name">价格</span>
            </div>
            <div class="parts-hr"></div>
            <div class="info-content">
                <el-form :model="form" :rules="rules" label-width="100px" class="parts-form-inline">
                    <div class="item-row">
                        <el-form-item label="采购价" prop="purchasePrice">
                            <el-input v-model="form.price.purchasePrice" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="利润率">
                            <el-input v-model="form.price.profitRate" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="4S价格">
                            <el-input v-model="form.price._4sPrice" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="基准销售价" prop="baseSalePrice">
                            <el-input v-model="form.price.baseSalePrice" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="item-row">
                        <el-form-item label="最低销售价">
                            <el-input v-model="form.price.minSalePrice" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="调拨价">
                            <el-input v-model="form.price.turnPrice" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                </el-form>
            </div>
        </div>
        <div class="suit-type block-item">
            <div class="header">
                <img src="./images/left.png" alt="">
                <span class="header-name">适用车型</span>
            </div>
            <div class="parts-hr"></div>
            <div class="info-content">
                <el-form :model="form" label-width="85px" class="parts-form-inline">
                    <div class="item-row">
                        <el-form-item label="是否通用">
                            <el-select v-model="form.suit.isCommon">
                                <el-option label="是" value="1"></el-option>
                                <el-option label="否" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div class="item-row" v-for="item in form.suit.suitType">
                        <el-form-item class="three-selected" :label="item.label">
                            <el-select v-model="form.suit.isCommon">
                                <el-option label="是" value="1"></el-option>
                                <el-option label="否" value="0"></el-option>
                            </el-select>
                            <el-select v-model="form.suit.isCommon">
                                <el-option label="是" value="1"></el-option>
                                <el-option label="否" value="0"></el-option>
                            </el-select>
                            <el-select v-model="form.suit.isCommon">
                                <el-option label="是" value="1"></el-option>
                                <el-option label="否" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                </el-form>
                <div class="underline-title finger suit" @click="newSuitType">新增适用车型</div>
            </div>
        </div>
        <div class="oe-oem block-item">
            <div class="header">
                <img src="./images/left.png" alt="">
                <span class="header-name">OE/OEM对应关系</span>
            </div>
            <div class="parts-hr"></div>
            <div class="info-content">
                <el-form :model="form.oeOem" label-width="100px" class="parts-form-inline-half">
                    <div class="item-row-half">
                        <div class="header-title">品牌</div>
                        <el-form-item class="staticOe" label="OE">原厂宝马</el-form-item>
                    </div>
                    <div class="item-row-half">
                        <div class="header-title">零件号</div>
                        <el-form-item class="staticOe" label="">原厂宝马</el-form-item>
                    </div>
                </el-form>
                <el-form class="parts-form-inline-half" :key="item.value" v-for="item in form.oeOem.list">
                    <div class="item-row" >
                        <el-form-item label="OEM">
                            <el-select v-model="item.brand" placeholder="品牌">
                                <el-option label="是" value="1"></el-option>
                                <el-option label="否" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" style="margin-left: 31px;">
                            <el-input v-model="item.number" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                </el-form>
                <div class="underline-title finger suit oe" @click="newTempNum">新增零件号</div>
            </div>
        </div>
        <div class="button-list">
            <el-button type="primary" @click="save">保存</el-button>
            <el-button type="primary" class="reset" @click="goBack">返回</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "new-parts-maintain",
        props: {
            showOrHide: {
                type: Boolean,
                default () {
                    return true
                }
            },
            editOrNew: {
                type: Boolean,
                default () {
                    return true;
                }
            }

        },
        data () {
            return {
                form: {
                    basicInfo: {
                        partsType: "",
                        partsBrand: "",
                        number: "",
                        tempNo: "",
                        quality: "",
                        partsPic: "",
                        partsName: "",
                        partsNikName: "",
                        specValue: "",
                        unitName: "",
                        note: "",
                        status: ""
                    },
                    price: {
                        purchasePrice: "",
                        profitRate: "",
                        _4sPrice: "",
                        baseSalePrice: "",
                        minSalePrice: "",
                        turnPrice: ""
                    },
                    suit: {
                        isCommon: "",
                        suitType: [
                            {
                                label: "适用车型1"
                            }
                        ]
                    },
                    oeOem: {
                        brand: [
                            {
                                name: "宝马",
                                value: "00222"
                            },
                            {
                                name: "奔驰",
                                value: "00223"
                            },
                        ],
                        number: "",
                        list: [
                            {
                                brand: "",
                                number: "",
                            }
                        ]
                    },
                    partsTypeTree: [
                        {
                            name: "汽车油料1",
                            value: "0001",
                            children: [{
                                name: "小汽车油料",
                                value: "0002"
                            }]
                        }, {
                            name: "发动机配件",
                            value: "0003",
                            children: [{
                                name: "配件1",
                                value: "0004"
                            }]
                        }
                    ],
                    partsBrand: [
                        {
                            name: "配件品牌1",
                            value: "00011"
                        }, {
                            name: "配件品牌2",
                            value: "00022"
                        },
                    ],
                    quality: [
                        {
                            name: "原厂",
                            value: "yuanchang"
                        }, {
                            name: "品牌",
                            value: "brand"
                        }, {
                            name: "改包",
                            value: "gaibao"
                        }, {
                            name: "拆车",
                            value: "chaiche"
                        }
                    ],
                    defaultProps: {
                        children: 'children',
                        label: 'name'
                    }
                },
                rules: {
                    partsType: [{ required: true, message: '请选择配件类型', trigger: 'change' }],
                    partsBrand: [{ required: true, message: '请选择配件品牌', trigger: 'change' }],
                    number: [{ required: true, message: '请输入和谐编号', trigger: 'blur' }],
                    quality: [{ required: true, message: '请选择质量标准', trigger: 'blur' }],
                    partsName: [{ required: true, message: '请输入配件名称', trigger: 'blur' }],
                    purchasePrice: [{ required: true, message: '请输入采购价格', trigger: 'blur' }],
                    baseSalePrice: [{ required: true, message: '请输入销售基准价', trigger: 'blur' }],
                }
            }
        },
        methods: {
            newTempNum () {
              var len = this.form.oeOem.list.length + 1;
              this.form.oeOem.list.push({
                  brand: "",
                  number: ""
              })
            },
            newSuitType () {
                var len = this.form.suit.suitType.length;
                this.form.suit.suitType.push({
                    label: "适用车型" + (+len + 1)
                });
            },
            save () {
//                this.$refs[this.form.basicInfo].validate((valid) => {
//                    if (valid) {
//                        alert('submit!');
//                    } else {
//                        console.log('error submit!!');
//                        return false;
//                    }
//                });
                this.showOrHide = false;
                this.$emit("toggle-status", this.showOrHide)
            },
            goBack () {
                this.showOrHide = false;
                this.$emit("toggle-status", this.showOrHide)
            }
        },
        created () {
            console.log(this.editOrHide)
        }
    }
</script>


