<script>
import headers from "@/components/Head";
export default {
	name: "partErrorCorrection",
	data() {
		return {
			activeTabName: 'first',
			form1: {
				ipt1: '',
				ipt2: '',
				ipt3: 'ffff',
				ipt4: 'aaaa',
				ipt5: '',
				ipt6: ''
			},
			form2: {
				ipt1: '',
				ipt2: '',
				ipt3: '机油滤清器',
				ipt4: '机油滤芯',
				ipt5: '120.00',
				ipt6: '110.00',
				ipt7: '',
				ipt8: ''
			},
			form3: {
				ipt1: ''
			},
			form4: {
				ipt1: '',
				ipt2: '',
				ipt3: ''
			},
			form5: {
				ipt1: '',
				ipt2: ''
			},
			form6: {
				ipt1: 'BM 11427640862-A',
				ipt2: '44%'
			},
			form7: {
				showPhoto1: true,
				photoSrc1: '',
				showPhoto2: true,
				photoSrc2: ''
			},
			form8: {
				ipt1: '',
				ipt2: ''
			},
			form9: {
				ipt1: 'BM 34534353',
				ipt2: '14%'
			},
			form10: {
				ipt1: '',
				ipt2: '',
				ipt3: ''
			},
			form11: {
				ipt1: '',
				showPhoto3: true,
				photoSrc3: ''
			},
			form12: {
				ipt1: '',
				ipt2: '',
				ipt3: ''
			},
			selectVal: '',
			options: [{
				value: 'dfdfd',
				label: 'dfdfff'
			}, {
				value: '34ed4ed',
				label: 'ddeeee'
			}, {
				value: 'df4r4t54',
				label: 'ddeeee'
			}],
			labelPosition: 'right',
			fileIptDisabled: false,
			showPhoto1: true,
			photoSrc1: '',
			showPhoto2: true,
			photoSrc2: ''
		};
	},
	components: { headers },
	methods: {
		handleClick () {

		},
		filePreview(e, _form, _showPhoto, _photSrc) {
			let This = this;
			var file = e.currentTarget.files[0],
				imageType = /^image\//;
			if (window.FileReader) {		//判断是否支持FileReader
				var reader = new FileReader();
			} else {
				console.log('您的设备不支持图片预览功能，如需该功能请升级您的设备！');
			}
			if (!imageType.test(file.type)) {	//是否是图片
				console.log('不是图片！');
				return;
			}
			reader.onload = function(e) {	//读取完成
				This[_form][_showPhoto] = false;
				console.log('_showPhoto: ', This[_showPhoto]);
				This[_form][_photSrc] = e.target.result;	//图片路径设置为读取的图片
			};
			reader.readAsDataURL(file);   // readAsDataURL:文件转化成base64编码的方法
		}
	},
	watch: {}
};
</script>

<style lang="less">
.main{
	padding: 0 20px;
}
.page-container {
	width: 100%;
	height: auto;
	overflow: hidden;
	margin: 0 auto;
	.fr {
		float: right;
	}
	.bread-crumb{
		height: 50px;
		background: #fff;
		margin-top: 10px;
		padding: 0 35px;
		span{
			display: block;
			float: left;
			margin-right: 5px;
			line-height: 50px;
			height: 50px;
		}
		.crumb-item{
			padding-right:15px;
			font-size: 16px;
			color: #5681C2;
			font-weight: 700;
		}
	}
	.work-content {
		height: auto;
		overflow: hidden;
		border-radius: 2px;
		padding: 10px;
		background: #fff;
		margin-top: 10px;
		.work-header {
			height: 30px;
			padding: 5px 0 8px;
			.header-title {
				display: inline-block;
				height: 30px;
				line-height: 30px;
				color: #999;
				font-weight: bold;
				font-size: 14px;
				padding-left: 10px;
				background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAICAYAAADeM14FAAAAAXNSR0IArs4c6QAAAC9JREFUCB1j2L93738GJMAEYqMLggVggoww1TABsBaYIJwGycJUoOgHq4DLQNUDAF67IDZBJJ1VAAAAAElFTkSuQmCC") no-repeat left center;
			}
			.header-tool {
				display: inline-block;
			}
		}
		.work-header-line {
			border-bottom: 1px solid #dbe4ed;
		}
		.work-wraper {
			height: auto;
			overflow: hidden;
			.grid-content {
				height: auto;
				overflow: hidden;
				border-top: 1px solid #dbe4ed;
				border-left: 1px solid #dbe4ed;
				border-radius: 4px;
				.grid-item {
					height: 40px;
					line-height: 40px;
					padding: 0 15px;
					border-bottom: 1px solid #dbe4ed;
					border-right: 1px solid #dbe4ed;
					.item-title {
						color: #2e2e2e;
						font-size: 14px;
						font-weight: bold;
						float: left;
						padding-right: 30px;
					}
					p {
						color: #6e6e6e;
						font-size: 12px;
					}
				}
			}
			.checkbox-button-box {
				height: auto;
				overflow: hidden;
			}
			.work-table {
				position: relative;
				padding-top: 15px;
				height: auto;
				overflow: hidden;
				.el-input-number--mini{
					width: 120px;
				}
				.el-input-number__decrease, .el-input-number__increase{
					top:2px;
				}
				.my-tabs{
					.el-tabs__header{
						margin-bottom: 0;
						// border: none;
					}
					.el-tabs__item{
						padding: 0 10px;
						height: 30px;
						line-height: 30px;
					}
					&>.el-tabs__header{
						.el-tabs__nav{
							border: none;
						}
						.el-tabs__item{
							margin-right: 2px;
							border: 1px solid #C1CCD8;
							border-radius: 4px 4px 0 0;
							font-size: 14px;
							color: #6E6E6E;
							&.is-active{
								border: 1px solid #5681C2;
								border-radius: 4px 4px 0 0;
								font-size: 14px;
								color: #5681C2;
							}
						}
					}
					.el-textarea{
						margin-left: 10px;
						textarea{
							resize: none;
						}
					}
					.el-input__inner, .el-textarea__inner{
						border-color: #C1D1E5;
					}
					.el-input__inner{
						margin-left: 10px;
						padding: 0 10px;
						height: 36px;
					}
				}
				.part-error-correct-form{
					padding-top: 20px;
					margin: 0 auto;
					width: 550px;
				}
				.el-form-item{
					margin-bottom: 20px;
					&.diffLabel{
						label{
							padding-top: 25px;
						}
					}
					.el-form-item__label{
						color: #2e2e2e;
					}
					.iptResult{
						margin-left: 10px;
						color: #6E6E6E;
						font-size: 12px;
					}
					.fileIpt{
						position: relative;
						margin-top: 12px;
						margin-left: 10px;
						display: inline-block;
						background: url("data:image/png;base64,") center 35% no-repeat;
						border: 3px solid #DBE4ED;
						width: 100px;
						height: 100px;
						box-sizing: border-box;
						input{
							display: inline-block;
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							opacity: 0;
							z-index: 2;
						}
						&:after{
							content: "上传照片";
							position: absolute;
							bottom: 13%;
							width: 100%;
							text-align: center;
							font-size: 12px;
							color: #6E6E6E;

						}
					}
					.photo{
						margin-left: 10px;
						margin-top: 12px;
						width: 100px;
						height: 100px;
						vertical-align: top;
					}
				}
				.tabContBox{
					padding: 0px 56px 14px;
					.colSel{
						width: 99%;
					}
				}
				.dotRow{
					position: relative;
					padding-left: 18px;
					font-size: 14px;
					line-height: 50px;
					color: #6E6E6E;
					clear: both;
					&:before{
						content: "";
						position: absolute;
						left: 0;
						top: 21px;
						width: 8px;
						height: 8px;
						border-radius: 50%;
						background: #C1CCD8;
					}
				}
				.addMoreSuitChoice{
					text-decoration: underline;
					font-size: 12px;
					color: #5681C2;
				}
			}
		}
	}
	.btnBox{
		margin: 40px auto 25px;
		display: flex;
		justify-content: space-between;
		width: 240px;
		.el-button--primary{
			padding: 7px 13px;
		}
		.el-button--mini{
