            border: 1px solid #5681C2;
            border-radius: 4px;
			font-size: 14px;
			color: #6E6E6E;
			background: none;
        }
	}
}


@media screen and (max-width: 1366px) {
		.page-container  {
				width: 96%;
				padding: 0 2%;
		}
}
</style>


