                    hxcode: "BM 11427640862-A",
                    ljcode: "11427640862",
                    ljname: "组件机油滤清器滤芯",
                    ljnickname: "组件机油滤清器滤芯",
                    price4S: "240.00",
                    price: "140.00",
                    count: 2,
                    total: "140.00"
                }, {
                    brand: "嘉实多",
                    hxcode: "CASTPOL JHZX 0W40-209L",
                    ljcode: "MO201543",
                    ljname: "嘉实多机油 极护专享0W-40 A3 209L",
                    ljnickname: "嘉实多机油 极护专享0W-40 A3 209L",
                    price4S: "98.50",
                    price: "78.50",
                    count: 1,
                    total: "78.50"
                }],
                checkList: [],
                page1: {
                    total: 196,
                    currentPage: 1,
                    totalPage: 6
                },
                page2: {
                    total: 200,
                    currentPage: 1,
                    totalPage: 12
                },
                dialogVisible: false,
                ruleForm: {
                    project: "",
                    classify: ""
                },
                rules: {}
            };
        },
        watch: {
            activeItem () {
                this.car.checkItem = [];
                this.car.carXiList.forEach(function (item) {
                    item.active = false;
                })
            }
        },
        components: {headers},
        methods: {
            chengeCurrentPage1: function (page) {
                this.page1 = page;
                console.log(this.page1)
            },
            chengeCurrentPage2: function (page) {
                this.page2 = page;
                console.log(this.page2)
            },
            showDialog: function () {
                this.dialogVisible = true;
            },
            checkType (item, param) {
                param.forEach(function (ee) {
                    ee.active = false;
                })
                item.active = true;
            },
            clickSelectType (param) {
                if (param == "right") {
                    this.dialog.typeSelect = true;
                } else {
                    this.dialog.typeSelect = false;
                }
            },
            toggleContent (param) {
                console.log(param)
                if (param == "1" ) return;
                if (param == "2") {
                    this.activeItem = "2";
                    this.stepList[1].active  = true;
                    this.stepList[2].active  = false;
                    this.stepList[3].active  = false;
                }
                if (param == "3") {
                    this.activeItem = "3";
                    this.stepList[param-1].active  = true;
                    this.stepList[param].active  = false;

                } else if (param == "4")  {
                    this.activeItem = "4";
                    this.stepList.forEach(function (item) {
                        item.active = true;
                    });
                }


            },
            addItem (item, index) {
//                if (item.active) {
//                    item.active = false;
//                    this.car.checkItem.splice(index, 1);
//                } else {
                    item.active = true;
                    this.car.checkItem.push({
                        name: item.name,
                        value: item.value
                    });
//                }
            },
            closeItem (index) {
                this.car.checkItem.splice(index, 1);
//                this.car.carXiList[index].active = false;
            }
        },
    };
</script>

<style lang="less">
