                        /*right: -19.5px;*/
                        /*width: 0;*/
                        /*height: 0;*/
                        /*border-bottom: 20px solid transparent;*/
                        /*border-left: 20px solid #fff;*/
                        /*border-top: 20px solid transparent;*/
                        /*}*/
                        .icon-clc {
                            position: relative;
                            width: 20px;
                            height: 20px;
                            border-radius: 20px;
                            background-color: #BFBDBD;
                            margin-right: 19px;
                            div {position: absolute; top: -19px; left: 6px; color: #fff; }
                        }
                    }
                    .left-single { position: absolute;right: -20px; width: 0;height: 0;border-top: 20px solid transparent;  border-left: 20px solid #E5E5E5;  border-bottom: 20px solid transparent; }
                }
            }
            .selected-type {
                height: auto;
                display: flex;
                align-items: center;
                div {
                    &:first-child {width: 150px;}
                }
                .item-row {
                    display: flex;
