                        &:nth-child(6) {margin-right: 0;}
                        div {width: 80%;}
                    }
                }
            }
            .selected-slect {height: 30px;margin-bottom: 10px;
                .el-select { height: 24px; width: 100px;
                    .el-input--suffix { height: 24px;
                        .el-input__inner {height: 100%}
