                    &.active { border: 1px solid #5681C2; }
                }
            }
            .button-list { position: absolute; left: 0; right: 0; bottom: 15px; margin: auto;height: 30px; width: 84px;
                button {height: 30px; width: 84px; background-color: #5681C2; color: #fff; padding: 0;}
