                                    </el-table-column>
                                    <el-table-column
                                        prop="ljcode"
                                        label="零件号"
                                        align="center"
                                        min-width="150">
                                    </el-table-column>
                                    <el-table-column
                                        prop="ljname"
                                        label="零件名称"
                                        align="center"
                                        min-width="150">
                                    </el-table-column>
                                    <el-table-column
                                        prop="ljnickname"
                                        label="零件俗称"
                                        align="center"
                                        min-width="150">
                                    </el-table-column>
                                    <el-table-column
                                        prop="price4S"
                                        label="4S点单价"
                                        align="center"
                                        min-width="100">
                                    </el-table-column>
                                    <el-table-column
                                        prop="price"
                                        label="本店单价"
                                        align="center"
                                        min-width="100">
                                    </el-table-column>
                                    <el-table-column
                                        prop="count"
                                        label="数量"
                                        align="center"
                                        min-width="150">
                                        <template slot-scope="scope">
                                            <el-input-number size="mini" :min="0" :max="Infinity" v-model="scope.row.count"></el-input-number>
                                        </template>
                                    </el-table-column>
                                    <el-table-column
                                        prop="total"
                                        label="金额"
                                        align="center"
                                        min-width="100">
                                    </el-table-column>
                                    <el-table-column
                                        label="操作"
                                        align="center"
                                        width="100">
                                        <template slot-scope="scope">
                                            <el-button @click="handleClick(scope.$index, multipleSelection)" type="text" size="small">删除</el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </el-tab-pane>
                            <el-tab-pane label="维修项目" name="second">
                                <el-table :data="tableData2" border style="width: 100%">
                                    <el-table-column
                                        prop="gscode"
