                                        align="center"
                                        min-width="150">
                                    </el-table-column>
                                    <el-table-column
                                        prop="gsprice"
                                        label="工时价格"
                                        align="center"
                                        min-width="150">
                                    </el-table-column>
                                    <el-table-column
                                        label="操作"
                                        align="center"
                                        min-width="100">
                                        <template slot-scope="scope">
                                            <el-button @click="handleClick(scope.$index, tableData2)" type="text" size="small">删除</el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </el-tab-pane>
                        </el-tabs>
                    </div>
                </div>
            </div>
            <div class="work-page-footer">
                <el-button type="primary">保存</el-button>
                <el-button type="primary" @click="cancelLinkTo">取消</el-button>
            </div>
        </div>
	</div>
</template>

<script>
import headers from "@/components/Head";
import CheckButtom from "@/pages/maintain/CheckButtom";
import Pagination from "@/pages/maintain/Pagination";
export default {
  name: "maintain",
  data() {
    return {
      isSelf:true,
      projectid:"",
      list1: [{
          id:"201",
          icon: "icon_ghjyjl",
          text: "更换机油机滤"
      }],
      list2: [{
          id:"202",
          icon: "icon_ghkqlx",
          text: "更换空气滤芯"
      },{
          id:"203",
          icon: "icon_ghktlx",
          text: "更换空调滤芯"
      },{
          id:"204",
          icon: "icon_ghrylx",
          text: "更换燃油滤芯"
      },{
          id:"205",
          icon: "icon_ghhhs",
          text: "更换火花塞"
      },{
          id:"206",
          icon: "icon_ghqscp",
          text: "更换前刹车片"
      },{
          id:"207",
          icon: "icon_ghhscp",
          text: "更换后刹车片"
      },{
          id:"208",
          icon: "icon_ghscp",
          text: "更换前刹车盘"
      },{
          id:"209",
          icon: "icon_ghscp",
          text: "更换后刹车盘"
      },{
          id:"210",
          icon: "icon_fdygp",
          text: "更换前风挡雨刮片"
      },{
          id:"211",
          icon: "icon_fdygp",
          text: "更换后风挡雨刮片"
      }],
      list3:[{
          id:"212",
          icon: "icon_ghscy",
          text: "更换刹车油"
      },{
          id:"213",
          icon: "icon_ghfdy",
          text: "更换防冻液"
      },{
          id:"214",
          icon: "icon_bsyx",
          text: "更换变速箱油"
      },{
          id:"215",
          icon: "icon_zxzly",
          text: "更换转向助力油"
      }],
      list4:[{
          id:"216",
          icon: "icon_dp",
          text: "更换灯泡"
      }],
      srarch:{
          classify:"",
          brand:""
      },
      activeName: "first",
      multipleSelection: [],
      tableData:[{
          brand:"宝马",
          hxcode:"BM 11427640862-A",
          ljcode:"11427640862",
          ljname:"组件机油滤清器滤芯",
          ljnickname:"组件机油滤清器滤芯",
          price4S:"240.00",
          price:"140.00",
          count:2,
          total:"140.00"
      },{
          brand:"嘉实多",
          hxcode:"CASTPOL JHZX 0W40-209L",
          ljcode:"MO201543",
          ljname:"嘉实多机油 极护专享0W-40 A3 209L",
          ljnickname:"嘉实多机油 极护专享0W-40 A3 209L",
          price4S:"98.50",
          price:"78.50",
          count:1,
          total:"78.50"
      }],
      tableData2:[],
      page:{
          total:196,
          currentPage:1,
          totalPage:6
      }
    };
  },
  beforeRouteEnter: (to, from, next) => {
    next((vm)=>{
        vm.projectid = vm.$route.query.id
    })
  },
  components: { headers, CheckButtom,Pagination },
  methods: {
    handleClick:function(index, rows){
        this.$confirm('确定删除该条数据吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.isPush = false;
            rows.splice(index, 1);
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    checkProject:function(project){
        this.$router.push({ query: { id: project.id }});
        this.projectid = project.id;
    },
    chengeCurrentPage:function(page){
        this.page = page;
        console.log(this.page)
    },
    handleSelectionChange(val) {
        this.multipleSelection = val;
    },
    linkErrorPage:function(){
        this.$router.push({ path:"/partErrorCorrection" });
    },
    cancelLinkTo:function(){
        this.$router.push({ path:"/workhelper" });
    }
  },
};
</script>

<style lang="less">
.bread-crumb{
    height: 50px;
    background: #fff;
    margin-top: 10px;
    padding: 0 35px;
    span{
        display: inline-block;
        float: left;
        margin-right: 5px;
        line-height: 50px;
        height: 50px;
    }
    .crumb-item{
        padding-right:15px;
        font-size: 16px;
        color: #BFBDBD;
        font-weight: 700;
        background: url(./images/icon_arrow_right_cur.png) no-repeat center right;
    }
    .crumb-item a{
        color: #BFBDBD;
        font-weight: 500;
    }
    .current-crumb{
        font-size: 16px;
        color: #5681C2;
    }
}
.work-page-footer{
    padding:40px 0;
    text-align: center;
    .el-button--primary{
        margin:0 50px;
    }
}
.my-select{
    margin-top:2px!important;
    .popper__arrow{
        display: none;
    }
}
.page-container {
  height: auto;
  overflow: hidden;
  margin: 0 auto;
  padding: 10px 20px;
  .fr {
    float: right;
  }
  
  .work-content {
    height: auto;
    overflow: hidden;
    border-radius: 2px;
    padding: 10px 15px 15px;
    background: #fff;
    margin-bottom: 10px;
    .search-bar{
        height: 30px;
        overflow: hidden;
        padding:5px 0 0;
        .search-item{
            display: inline-block;
            height: 30px;
            >span{
                display: inline-block;
                font-size: 14px;
                color: #6E6E6E;
                margin-right: 5px;
                line-height: 26px;
            }
            .el-input__inner{
                padding: 0 5px;
                border-color: #DBE4ED;
            }
        }
        .left-item{
            .el-select{
                width: 75px;
            }
        }
        .item-center{
            text-align: center;
            padding-top: 2px;
            .el-input--mini .el-input__inner{
                width: 390px;
                border-radius: 4px;
                margin-right: 5px;
                border-color: #DBE4ED;
            }
            .el-input-group__append{
                border: 1px solid #DBE4ED;
                border-radius: 4px;
                background: #fff;
                .el-button--default{
                    padding: 0;
                    margin-top:-26px;
                }
                .el-button--default span{
                    margin-top: 0;
                }
            }
        }
        .item-right{
            float: right;
            font-size: 14px;
            color: #6E6E6E;
            line-height: 26px;
            span{
                color: #d52b2e;
                margin: 0 5px;
            }
        }
        .mgr25{
            margin-right: 25px;
        }
    }
    .work-header {
      height: 30px;
      padding: 5px 0;
      .header-title {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        color: #999;
        font-weight: bold;
        font-size: 14px;
        padding-left: 10px;
        background: url(./images/icon_arrow-right.png) no-repeat left center;
      }
      .header-checkbox{
        display: inline-block;
        height: 30px;
        line-height: 30px;
        margin-left: 50px;
        .el-checkbox__input{
            margin-top:2px;
        }
        .el-checkbox__label{
            font-size: 14px;
            color: #808080;
        }
      }
    }
    .work-wraper {
      height: auto;
      overflow: hidden;
      .grid-content {
        height: auto;
        overflow: hidden;
        border-top: 1px solid #dbe4ed;
        border-left: 1px solid #dbe4ed;
        border-radius: 4px;
        .grid-item {
          height: 40px;
          line-height: 40px;
          padding: 0 15px;
          border-bottom: 1px solid #dbe4ed;
          border-right: 1px solid #dbe4ed;
          .item-title {
            color: #2e2e2e;
            font-size: 14px;
            font-weight: bold;
            float: left;
            padding-right: 30px;
            font-weight: 500;
          }
          p {
            color: #6e6e6e;
            font-size: 12px;
          }
        }
      }
      .maintain-box{
          height: auto;
          overflow: hidden;
          .maintain-item-title{
              display: inline-block;
              float: left;
              height: 36px;
              line-height: 36px;
              font-size: 14px;
              color: #2E2E2E;
              width: 110px;
              margin: 10px 0;
              padding-left: 10px;
          }
          .checkbox-button-box {
            height: auto;
            overflow: hidden;
            margin-left: 110px;
          }
      }
      .work-table {
        position: relative;
        padding-top: 15px;
        height: auto;
        overflow: hidden;
        .el-table th{
            border-bottom: 2px solid #e6ebf5;
        }
        .el-table-column--selection .cell{
            padding: 0 10px;
        }
        .el-table{
            border-bottom-left-radius: 4px;
            border-bottom-right-radius: 4px;
        }
        .el-table .cell{
            font-size: 12px;
            color: #6E6E6E;
        }
        .el-table th>.cell{
            font-size: 14px;
            color: #2E2E2E;
        }
        .el-button--text{
           color: #5681C2;
           text-decoration: underline;
        }
        .el-table td{
            padding: 5px 0;
        }
        .el-input-number--mini{
            width: 120px;
            height: 32px;
        }
        .el-input-number__decrease,
        .el-input-number__increase{
            height: 28px;
            top: 1px;
            border-radius: 0;
            background: #fff;
        }
        .el-input--mini .el-input__inner{
            height: 30px;
            border-radius: 0;
        }
        .my-tabs .el-tabs__header{
            margin-bottom: 1px;
            border:none;
        }
        .my-tabs .el-tabs__header .el-tabs__nav{
            border:none;
        }
        .my-tabs .el-tabs__item{
            padding: 0 10px;
            height: 30px;
            line-height: 30px;
        }
        .my-tabs>.el-tabs__header .el-tabs__item{
            border: 1px solid #C1CCD8;
            border-radius: 4px 4px 0 0;
            font-size: 14px;
            color: #6E6E6E;
            margin-right:2px;
        }
        .my-tabs>.el-tabs__header .el-tabs__item.is-active{
            border: 1px solid #5681C2;
            border-radius: 4px 4px 0 0;
            font-size: 14px;
            color: #5681C2;
        }
        .table-bottom-wraper{
            padding: 16px 0 0;
            height: 20px;
            .table-bottom-left{
                display: inline-block;
                float: left;
                p{
                    font-size: 12px;
                    color: #6E6E6E;
                    span{
                        font-size: 14px;
                        color: #5681C2;
                        margin-left: 20px;
                        cursor: pointer;
                    }
                }
            }
        }
      }
      .input-number-table{
            .el-input-number__decrease,
            .el-input-number__increase{
                top:1px!important;
            }
      }
    }
  }
}

</style>


