<template>
	<div class="page">
        <headers></headers>
        <div class="bread-crumb">
            <span class="crumb-item"><router-link to="/workhelper">工单助手</router-link></span>
            <span class="current-crumb">美容清洗</span>
        </div>
        <div id="main" class="page-container">
            <div class="work-content">
                <div class="work-wraper">
                    <div class="maintain-box">
                        <label class="maintain-item-title">洗车</label>
                        <div class="checkbox-button-box">
                            <CheckButtom v-for="(item,index) in list1" :key="index" :item="item" :id="projectid" @click.native.prevent="checkProject(item)"></CheckButtom>
                        </div>
                    </div>
                    <div class="maintain-box">
                        <label class="maintain-item-title">漆面</label>
                        <div class="checkbox-button-box">
                            <CheckButtom v-for="(item,index) in list2" :key="index" :item="item" :id="projectid" @click.native.prevent="checkProject(item)"></CheckButtom>
                        </div>
                    </div>
                    <div class="maintain-box">
                        <label class="maintain-item-title">轮胎</label>
                        <div class="checkbox-button-box">
                            <CheckButtom v-for="(item,index) in list3" :key="index" :item="item" :id="projectid" @click.native.prevent="checkProject(item)"></CheckButtom>
                        </div>
                    </div>
                    <div class="maintain-box">
                        <label class="maintain-item-title">发动机舱</label>
                        <div class="checkbox-button-box">
                            <CheckButtom v-for="(item,index) in list4" :key="index" :item="item" :id="projectid" @click.native.prevent="checkProject(item)"></CheckButtom>
                        </div>
                    </div>
                    <div class="maintain-box">
                        <label class="maintain-item-title">内饰</label>
                        <div class="checkbox-button-box">
                            <CheckButtom v-for="(item,index) in list5" :key="index" :item="item" :id="projectid" @click.native.prevent="checkProject(item)"></CheckButtom>
                        </div>
                    </div>
                    <div class="maintain-box">
                        <label class="maintain-item-title">其它</label>
                        <div class="checkbox-button-box">
                            <CheckButtom v-for="(item,index) in list6" :key="index" :item="item" :id="projectid" @click.native.prevent="checkProject(item)"></CheckButtom>
                        </div>
                    </div>
                </div>
            </div>
            <div class="work-content">
                <div class="work-wraper">
                    <div class="search-bar">
                        <el-row>
                            <el-col :span="24"><div class="search-item item-right">已选择<span>{{multipleSelection.length}}</span>个商品</div></el-col>
                        </el-row>
                    </div>
                    <div class="work-table">
                        <el-table :data="tableData" ref="multipleTable" border style="width: 100%;" @selection-change="handleSelectionChange">
                            <el-table-column
                                type="selection"
                                align="center"
                                width="40">
                            </el-table-column>
                            <el-table-column
                                prop="gscode"
                                label="工时代码"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="gsname"
                                label="工时名称"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="basenum"
                                label="标准工时数"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="gssingleprice"
                                label="工时单价"
                                align="center">
                            </el-table-column>
                            <el-table-column
                                prop="gsprice"
                                label="工时价格"
                                align="center">
                            </el-table-column>
                        </el-table>
                        <div class="table-bottom-wraper">
                            <div class="table-bottom-left"><span @click="linkErrorPage">我来纠错</span></div>
                            <Pagination :page="page" @chengeCurrentPage="chengeCurrentPage"></Pagination>
                        </div>
                    </div>
                </div>
            </div>
            <div class="work-page-footer">
                <el-button type="primary">保存</el-button>
                <el-button type="primary" @click="cancelLinkTo">取消</el-button>
            </div>
        </div>
	</div>
</template>

<script>
import headers from "@/components/Head";
import CheckButtom from "@/pages/Clean/CheckButtom";
import Pagination from "@/pages/Clean/Pagination";
export default {
  name: "Clean",
  data() {
    return {
      projectid:"",
      list1: [{
          id:"101",
          icon: "icon_xc",
          text: "洗车"
      }],
      list2: [{
          id:"102",
          icon: "icon_qcdl",
          text: "全车打蜡（普通）"
      },{
          id:"103",
          icon: "icon_qmdj",
          text: "漆面镀晶"
      },{
          id:"104",
          icon: "icon_qcpg",
          text: "全车抛光"
      },{
          id:"105",
          icon: "icon_dmpg",
          text: "单面抛光"
      },{
          id:"106",
          icon: "icon_qcfy",
          text: "全车封釉"
      }],
      list3:[{
          id:"107",
          icon: "icon_ltyh",
          text: "轮胎养护"
      },{
          id:"108",
          icon: "icon_lgcx",
          text: "轮毂除锈"
      }],
      list4:[{
          id:"109",
          icon: "icon_fdjcbmqx",
          text: "发动机舱表面清洗"
      }],
      list5:[{
          id:"110",
          icon: "icon_qcnsqjqx",
          text: "汽车内饰清洁清洗"
      }],
      list6:[{
          id:"111",
          icon: "icon_qdblqsdm",
          text: "前挡玻璃驱水镀膜"
      }],
      srarch:{
          classify:"",
          brand:""
      },
      activeName: "first",
      multipleSelection: [],
      tableData:[{
          gscode:"10012",
          gsname:"发动机舱表面清洗",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      }],
      page:{
          total:196,
          currentPage:1,
          totalPage:6
      }
    };
  },
  beforeRouteEnter: (to, from, next) => {
    next((vm)=>{
        vm.projectid = vm.$route.query.id
    })
  },
  mounted:function(){
      if(this.tableData.length==1){
          this.tableData.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
      }else{
          this.$refs.multipleTable.clearSelection();
      }
  },
  components: { headers, CheckButtom,Pagination },
  methods: {
    checkProject:function(project){
        this.$router.push({ query: { id: project.id }});
        this.projectid = project.id;
    },
    chengeCurrentPage:function(page){
        this.page = page;
        console.log(this.page)
    },
    handleSelectionChange(val) {
        this.multipleSelection = val;
    },
    linkErrorPage:function(){
        this.$router.push({ path:"/partErrorCorrection" });
    },
    cancelLinkTo:function(){
        this.$router.push({ path:"/workhelper" });
    }
  },
};
</script>

<style lang="less">
.bread-crumb{
    height: 50px;
    background: #fff;
    margin-top: 10px;
    padding: 0 35px;
    span{
        display: inline-block;
        float: left;
        margin-right: 5px;
        line-height: 50px;
        height: 50px;
    }
    .crumb-item{
        padding-right:15px;
        font-size: 16px;
        color: #BFBDBD;
        font-weight: 700;
        background: url(./images/icon_arrow_right_cur.png) no-repeat center right;
    }
    .crumb-item a{
        color: #BFBDBD;
        font-weight: 500;
    }
    .current-crumb{
        font-size: 16px;
        color: #5681C2;
    }
}
.work-page-footer{
    padding:40px 0;
    text-align: center;
    .el-button--primary{
        margin:0 50px;
    }
}
.page-container {
  height: auto;
  overflow: hidden;
  margin: 0 auto;
  padding: 10px 20px;
  .fr {
    float: right;
  }
  
  .work-content {
    height: auto;
    overflow: hidden;
    border-radius: 2px;
    padding: 10px 15px 15px;
    background: #fff;
    margin-bottom: 10px;
    .search-bar{
        height: 30px;
        overflow: hidden;
        padding:5px 0 0;
        .search-item{
            display: inline-block;
            height: 30px;
            >span{
                display: inline-block;
                font-size: 14px;
                color: #6E6E6E;
                margin-right: 10px;
                line-height: 20px;
            }
            .el-input__inner{
                padding: 0 5px;
            }
            .el-select--mini{
                width: 100px;
            }
        }
        .item-right{
            float: right;
            font-size: 14px;
            color: #6E6E6E;
            line-height: 26px;
            span{
                color: #d52b2e;
                margin: 0 5px;
            }
        }
        .mgr30{
            margin-right: 25px;
        }
    }
    .work-header {
      height: 30px;
      padding: 5px 0;
      .header-title {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        color: #999;
        font-weight: bold;
        font-size: 14px;
        padding-left: 10px;
        background: url(./images/icon_arrow-right.png) no-repeat left center;
      }
      .header-checkbox{
        display: inline-block;
        height: 30px;
        line-height: 30px;
        margin-left: 50px;
        .el-checkbox__input{
            margin-top:2px;
        }
        .el-checkbox__label{
            font-size: 14px;
            color: #808080;
        }
      }
    }
    .work-wraper {
      height: auto;
      overflow: hidden;
      .grid-content {
        height: auto;
        overflow: hidden;
        border-top: 1px solid #dbe4ed;
        border-left: 1px solid #dbe4ed;
        border-radius: 4px;
        .grid-item {
          height: 40px;
          line-height: 40px;
          padding: 0 15px;
          border-bottom: 1px solid #dbe4ed;
          border-right: 1px solid #dbe4ed;
          .item-title {
            color: #2e2e2e;
            font-size: 14px;
            font-weight: bold;
            float: left;
            padding-right: 30px;
            font-weight: 500;
          }
          p {
            color: #6e6e6e;
            font-size: 12px;
          }
        }
      }
      .maintain-box{
          height: auto;
          overflow: hidden;
          .maintain-item-title{
              display: inline-block;
              float: left;
              height: 36px;
              line-height: 36px;
              font-size: 14px;
              color: #2E2E2E;
              width: 110px;
              margin: 10px 0;
              padding-left: 10px;
          }
          .checkbox-button-box {
            height: auto;
            overflow: hidden;
            margin-left: 110px;
          }
      }
      .work-table {
        position: relative;
        padding-top: 15px;
        height: auto;
        overflow: hidden;
        .el-table th{
            border-bottom: 2px solid #e6ebf5;
        }
        .el-table-column--selection .cell{
            padding: 0 10px;
        }
        .el-table{
            border-bottom-left-radius: 4px;
            border-bottom-right-radius: 4px;
        }
        .el-table .cell{
            font-size: 12px;
            color: #6E6E6E;
        }
        .el-table th>.cell{
            font-size: 14px;
            color: #2E2E2E;
        }
        .el-button--text{
           color: #5681C2;
        }
        .el-table td{
            padding: 5px 0;
        }
        .el-input-number--mini{
            width: 120px;
        }
        .el-input-number__decrease,
        .el-input-number__increase{
            top:2px;
        }
        .my-tabs .el-tabs__header{
            margin-bottom: 1px;
            border:none;
        }
        .my-tabs .el-tabs__header .el-tabs__nav{
            border:none;
        }
        .my-tabs .el-tabs__item{
            padding: 0 10px;
            height: 30px;
            line-height: 30px;
        }
        .my-tabs>.el-tabs__header .el-tabs__item{
            border: 1px solid #C1CCD8;
            border-radius: 4px 4px 0 0;
            font-size: 14px;
            color: #6E6E6E;
            margin-right:2px;
        }
        .my-tabs>.el-tabs__header .el-tabs__item.is-active{
            border: 1px solid #5681C2;
            border-radius: 4px 4px 0 0;
            font-size: 14px;
            color: #5681C2;
        }
        .table-bottom-wraper{
            padding: 16px 0 0;
            height: 20px;
            .table-bottom-left{
                display: inline-block;
                float: left;
                span{
                    font-size: 14px;
                    color: #5681C2;
                    cursor: pointer;
                }
            }
        }
      }
    }
  }
}

</style>


