<script>
import headers from "@/components/Head";
import CheckButtom from "@/pages/workHelper/CheckButtom";
import Pagination from "@/pages/workHelper/Pagination";
export default {
  name: "workhelper",
  data() {
    return {
        projectList:[{
            text:"01037换机油",
            value:"1"
        },{
            text:"00149 检修、保养发电机",
            value:"2"
        },{
            text:"00983 +650",
            value:"3"
        },{
            text:"01025 1.6万公里常规保养、检查底盘、灯光、胎压等",
            value:"4"
        },{
            text:"A00001 10000公里保养",
            value:"5"
        },{
            text:"00772 1554456",
            value:"6"
        },{
            text:"00772 1554456方便",
            value:"7"
        },{
            text:"00772 1554456 2机油",
            value:"8"
        },{
            text:"00772 15544563 2万公里大保养",
            value:"9"
        },{
            text:"00772 36全车检测",
            value:"10"
        }],
      hotList: [
        {
          id:"101",
          icon: "icon_xc",
          text: "洗车",
          link:"/clean"
        },
        {
          id:"201",
          icon: "icon_ghjyjl",
          text: "更换机油机滤",
          link:"/maintain"
        }
      ],
      activeName: "first",
      tableData:[{
          brand:"宝马",
          hxcode:"BM 11427640862-A",
          ljcode:"11427640862",
          ljname:"组件机油滤清器滤芯",
          ljnickname:"组件机油滤清器滤芯",
          price4S:"240.00",
          price:"140.00",
          count:2,
          total:"140.00"
      },{
          brand:"嘉实多",
          hxcode:"CASTPOL JHZX 0W40-209L",
          ljcode:"MO201543",
          ljname:"嘉实多机油 极护专享0W-40 A3 209L",
          ljnickname:"嘉实多机油 极护专享0W-40 A3 209L",
          price4S:"98.50",
          price:"78.50",
          count:1,
          total:"78.50"
      }],
      isPush:false,
      tableData2:[{
          gscode:"10012",
          gsname:"发动机舱表面清洗",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      },{
          gscode:"10012",
          gsname:"发动机舱表面清洗",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      },{
          gscode:"10012",
          gsname:"发动机舱表面清洗",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      },{
          gscode:"10012",
          gsname:"发动机舱表面清洗",
          basenum:"10",
          gssingleprice:"24.00",
          gsprice:"280.00"
      }],
      page1:{
          total:196,
          currentPage:1,
          totalPage:6
      },
      page2:{
          total:200,
          currentPage:1,
          totalPage:12
      },
      dialogVisible:false,
      ruleForm: {
          project:"",
          classify:"",
          time:"",
          singlePrice:"",
          price:"",
          remark:""
      },
      rules: {

      },
      searchQuery:"",
      searchPopover:false
    };
  },
  components: { headers, CheckButtom,Pagination },
  computed:{
    conditions:function () {
        var items = this.projectList;
        var result=[];
        if (this.searchQuery !== '') {

            items.forEach((item) => {
                if(item.text.indexOf(this.searchQuery)!=-1){
                    result.push(item);
                }
            });
            return result;
        } else {
            return items;
        }
    }
  },
  methods: {
    handleClick:function(index, rows){
        this.$confirm('确定删除该条数据吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.isPush = false;
            rows.splice(index, 1);
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
        
    },
    chengeCurrentPage1:function(page){
        this.page1 = page;
        console.log(this.page1)
    },
    chengeCurrentPage2:function(page){
        this.page2 = page;
        console.log(this.page2)
    },
    showDialog:function(){
        this.dialogVisible = true;
    },
    hidePopover:function(item){
        this.ruleForm.project = item.text;
        this.searchPopover = false;
    },
    pushRow:function(){
        if(!this.isPush){
            this.isPush = true;
            this.tableData2.push({
                gscode:"10012",
                gsname:"",
                basenum:"",
                gssingleprice:"24.00",
                gsprice:"280.00"
            });
        }
    },
    saveClick:function(row){
        if(row.gsname!=''&&row.basenum!=''){
            this.isPush = false;
        }else{
            this.$message({
                message: '工时名称和标准工时数不能为空！',
                type: 'warning'
            });
        }
    }
  }
};
</script>

