                }
                p{
                    margin-left: 60px;
                    font-size: 12px;
                    color: #C91523;
                }
            }
            .add-link{
                float: right;
                margin-right: 50px;
                .a-tag-link{
                    font-size: 12px;
                    color: #5681C2;
                    cursor: pointer;
                }
            }
        }
        .table-bottom-wraper{
            padding: 16px 0;
            height: 20px;
        }
      }
      .input-number-table{
            .el-input-number__decrease,
            .el-input-number__increase{
                top:1px!important;
            }
      }
      .work-table-footer{
          padding-top:40px;
          text-align: center;
          .el-button--primary{
              padding: 7px 10px;
          }
      }
    }
  }
}


