<template>
    <div class="parts-mainten">
        <Headers></Headers>
        <div class="separator">
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :key="item" v-for="item in breadList">{{ item }}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="basic-info block" v-show="!editOrNew.showOrHide">
            <el-form :inline="true" :model="form" label-width="85px" class="parts-form-inline">
                <div class="item-row">
                    <el-form-item label="配件品牌">
                        <el-input v-model="form.brand" placeholder="配件品牌"></el-input>
                    </el-form-item>
                    <el-form-item label="和谐编号">
                        <el-input v-model="form.number" placeholder="和谐编号"></el-input>
                    </el-form-item>
                    <el-form-item label="零件号">
                        <el-input v-model="form.tempNum" placeholder="零件号"></el-input>
                    </el-form-item>
                </div>
                <div class="item-row">
                    <el-form-item label="配件名称">
                        <el-input v-model="form.partName" placeholder="配件名称"></el-input>
                    </el-form-item>
                    <el-form-item label="配件俗称">
                        <el-input v-model="form.partsNikName" placeholder="配件俗称"></el-input>
                    </el-form-item>
                    <el-form-item label="适用车型">
                        <el-input v-model="form.suitType" placeholder="适用车型"></el-input>
                    </el-form-item>
                </div>
            </el-form>
            <div class="button-list">
                <div class="button-box">
                    <el-button type="primary">查询</el-button>
                    <el-button type="primary" class="reset" @click="reset">重置</el-button>
                </div>
            </div>
            <div class="parts-hr"></div>
            <div class="new-button">
                <el-button type="primary" @click="newParts">新增</el-button>
            </div>
            <div class="parts-form">
                <el-table :data="table.list" height="250" type=index border style="width: 100%">
                    <el-table-column prop="brand" label="品牌" align="center" width="80"></el-table-column>
                    <el-table-column prop="num" label="和谐编号" align="center" width="200"></el-table-column>
                    <el-table-column prop="tempNum" label="零件号" align="center" width="100"></el-table-column>
                    <el-table-column prop="partsName" label="零件名称" align="center" width="235"></el-table-column>
                    <el-table-column prop="partsNikeName" label="零件俗称" align="center" width="235"></el-table-column>
                    <el-table-column prop="purchasePrice" label="采购价" align="center" width="80"></el-table-column>
                    <el-table-column prop="_4sPrice" label="4S价格" align="center" width="80"></el-table-column>
                    <el-table-column prop="basicSalePrice" label="基本销售价" align="center" width="100"></el-table-column>
                    <el-table-column prop="suitType" label="适用车型" align="center" width="80">
                        <template slot-scope="scope">
                            <div v-if="scope.row.suitType">
                                <el-button type="text" class="solt-button" @click="showPartsSuitType($event)"
                                        size="small">查看
                                </el-button>
                            </div>
                            <div v-else>通用</div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="oeOem" label="OE-OEM关系" align="center" width="90">
                        <template slot-scope="scope">
                            <el-button type="text" class="solt-button" @click="showPartsDialog($event)" size="small">
                                查看
                            </el-button>
                        </template>
                    </el-table-column>
                    <el-table-column prop="operate" label="操作" width="100" align="center">
                        <template slot-scope="scope" class="align-center">
                            <el-button type="text" class="solt-button" size="small" @click="editItem">修改</el-button>
                            <el-button type="text" class="solt-button" size="small" @click="delItem(scope.row.index)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="parts-pagination">
                    <Pager :page="page" @chengeCurrentPage="chengeCurrentPage"></Pager>
                </div>
            </div>
            <!--OE-OEM弹窗-->
            <el-dialog :visible.sync="dialog.show.oeOem" class="oe-oem-dialog" :modal=false height="265" width="500">
                <span slot="title">OE-OEM关系</span>
                <el-table :data="dialog.list.oeOem" class="parts-oa-table" border height="180" width="500">
                    <el-table-column prop="brand" label="品牌" align="center" width="100"></el-table-column>
                    <el-table-column prop="tempNum" label="和谐编号" align="center" width="400"></el-table-column>
                </el-table>
            </el-dialog>
            <!--试用车型弹窗-->
            <el-dialog :visible.sync="dialog.show.suitType" class="oe-oem-dialog" :modal=false height="265" width="500">
                <span slot="title">试用车型</span>
                <el-table :data="dialog.list.suitType" class="parts-oe-table" border height="180" width="500">
                    <el-table-column prop="brand" label="品牌" align="center" width="100"></el-table-column>
                    <el-table-column prop="carTrain" label="车系" align="center" width="100"></el-table-column>
                    <el-table-column prop="carType" label="车型" align="center" width="300"></el-table-column>
                </el-table>
            </el-dialog>
        </div>
        <NewParts :showOrHide="editOrNew.showOrHide" v-on:toggle-status="toggleStatus"
                :editOrNew="editOrNew.status"></NewParts>
    </div>
</template>
<script>
    import '../../assets/less/ylc.less';
    import Headers from '@/components/Head';
    import Pager from "@/pages/partsMaintain/Pagination";
    import NewParts from "@/pages/partsMaintain/NewParts";
    export default{
        data(){
            return {
                editOrNew: {
                    showOrHide: false,
                    status: true
                },
                breadList: ["配件维护"],
                form: {
                    brand: '',
                    number: '',
                    tempNum: '',
                    partName: '',
                    partsNikName: '',
                    suitType: '',
                },
                // 分页信息
                page: {
                    total: 200,
                    currentPage: 1,
                    totalPage: 12
                },
                // 表格信息
                table: {
                    list: [
                        {
                            brand: '原厂宝马',
                            num: 'BM 11427640862-A',
                            tempNum: '11427640862',
                            partsName: '组件机油滤清器滤芯',
                            partsNikeName: '机油滤清器芯',
                            purchasePrice: '100.00',
                            _4sPrice: '187.81',
                            basicSalePrice: '140.00',
                            suitType: false
                        }, {
                            brand: '原厂宝马',
                            num: 'BM 11427640862-A',
                            tempNum: '11427640862',
                            partsName: '组件机油滤清器滤芯',
                            partsNikeName: '机油滤清器芯',
                            purchasePrice: '100.00',
                            _4sPrice: '187.81',
                            basicSalePrice: '140.00',
                            suitType: true
                        }, {
                            brand: '原厂宝马',
                            num: 'BM 11427640862-A',
                            tempNum: '11427640862',
                            partsName: '组件机油滤清器滤芯',
                            partsNikeName: '机油滤清器芯',
                            purchasePrice: '100.00',
                            _4sPrice: '187.81',
                            basicSalePrice: '140.00',
                            suitType: true
                        }],
                },
                //弹窗信息
                dialog: {
                    show: {
                        oeOem: false,
                        suitType: false
                    },
                    list: {
                        oeOem: [
                            {
                                brand: '品牌',
                                tempNum: '王小虎'
                            }, {
                                brand: '品牌',
                                tempNum: '王小虎'
                            }, {
                                brand: '品牌',
                                tempNum: '王小虎'
                            }, {
                                brand: '品牌',
                                tempNum: '王小虎'
                            }],
                        suitType: [
                            {
                                brand: "宝马",
                                carTrain: "途观",
                                carType: "途观2015豪华版"
                            }, {
                                brand: "宝马",
                                carTrain: "途观",
                                carType: "途观2015豪华版"
                            }, {
                                brand: "宝马",
                                carTrain: "途观",
                                carType: "途观2015豪华版"
                            }
                        ]
                    }
                },
            }
        },
        components: {Headers, Pager, NewParts},
        watch: {
            editOrNew: {
                handler (newV) {
                    console.log(newV);
                },
                deep: true
            }
        },
        methods: {
            showPartsDialog (el) {
                console.log(el)
                this.dialog.show.oeOem = true;
            },
            showPartsSuitType () {
                this.dialog.show.suitType = true;
            },
            chengeCurrentPage (page) {
                this.page = page;
            },
            toggleStatus (showOrHide) {
                this.editOrNew.showOrHide = showOrHide;
                this.breadList.splice(1, 1);
            },
            reset () {
                var _this = this;
                for (var i in this.form) {
                    _this.form[i] = "";
                }
            },
            newParts () {
                this.editOrNew.showOrHide = true;
                this.editOrNew.status = true;
                this.breadList.push("新增配件");
            },
            editItem () {
                this.editOrNew.showOrHide = true;
                this.editOrNew.status = false;
                this.breadList.push("修改配件");
            },
            delItem (index) {
                this.table.list.splice(index, 1);
            }
        }
    }
</script>



