<template>
  <div :class="isCheck?'checkbox-item checkbox-item-hove':'checkbox-item'" @click="linkTo">
      <span :class="isCheck?'icon-box '+item.icon+'_hover':'icon-box '+item.icon"></span>
      <p>{{item.text}}</p>
      <input type="checkbox" v-model="isCheck" v-if="type!='link'"/>
      <i v-if="isCheck"></i>
  </div>
</template>

<script>
	export default {
        name: 'CheckButtom',
        data() {
			return {
                isCheck:""
			}
        },
        props:["item","type"],
        methods:{
            linkTo:function(){
                if(this.type=="link"){
                    this.$router.push({ path: this.item.link, query: { id: this.item.id }})
                }
            }
        }
	}
</script>

<style lang="less" scoped>
.checkbox-item{
    position: relative;
    display: inline-block;
    width: 180px;
    border: 1px solid #DBE4ED;
    border-radius: 2px;
    height: 34px;
    line-height: 34px;
    margin-right: 10px;
    cursor: pointer;
    input[type='checkbox'],
    input[type='radio']{
        position: absolute;
        display: inline-block;
        opacity: 0;
        outline: none;
        width: 100%;
        height: 100%;
        cursor: pointer;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }
    i{
        position: absolute;
        display: inline-block;
        width: 20px;
        height: 20px;
        top: 0;
        right: 0;
        background: url(./images/icon_check.png) no-repeat center center;
    }
    p{
        font-family: MicrosoftYaHei;
        width: 160px;
        font-size: 14px;
        color: #6E6E6E;
        text-align: center;
    }
    .icon-box{
        display: inline-block;
        height: 24px;
        width: 24px;
        float: left;
        margin: 5px;
    }
    .icon_xc{
        background: url(./images/icon_xc.png) no-repeat center center;
    }
    .icon_xc_hover{
        background: url(./images/icon_xc_hover.png) no-repeat center center;
    }
    .icon_ghjyjl{
        background: url(./images/icon_ghjyjl.png) no-repeat center center;
    }
    .icon_ghjyjl_hover{
        background: url(./images/icon_ghjyjl_hover.png) no-repeat center center;
    }
    
}
.checkbox-item-hove{
    border: 1px solid #5681C2;
    p{
        color: #5681C2;
    }
}
</style>




